import React, { Component } from 'react';
import './App.css';
import profilePic from './assets/img/photo.jpg';
import coverPage from './assets/img/cover_page.jpg';
import feesImg from './assets/img/ic_action_fee.png';
import attendanceImg from './assets/img/ic_action_attendance.png';
import marksImg from './assets/img/ic_action_marks.png';
import ic_name from './assets/img/ic_name.png';
import ic_diary from './assets/img/ic_diary.png';
import ic_mail from './assets/img/ic_mail.png';
import ic_password from './assets/img/ic_password.png';
import ic_hat from './assets/img/ic_hat.png';
import ic_username from './assets/img/ic_username.png';
import ic_phone from './assets/img/ic_phone.png';
import CircleWithText from './components/CircleWithText';
import CircleWithImage from './components/CircleWithImage';
import IconWithText from './components/IconWithText';
import NavigationHeader from './components/NavigationHeader';
import ProfileHeader from './components/ProfileHeader';
import InlineIconWithTextMenu from './components/InlineIconWithTextMenu';
import UserProfileInfo from './components/UserProfileInfo';

class App extends Component {
    render() {
        let iconList = [
            {
                "name": "arrow_back",
                "color": "white"
            },
            {
                "name": "settings",
                "color": "white"
            }
        ];

        let profileHeaderImages = 
        {
            "profilePic": profilePic,
            "coverPage": coverPage
        }

        let iconWithTextList = [
            {
                "src": feesImg,
                "height": "34px",
                "width": "28px",
                "text": "Fees",
                "link": "#"
            },
            {
                "src": attendanceImg,
                "height": "34px",
                "width": "28px",
                "text": "Attendance",
                "link": "#"
            },
            {
                "src": marksImg,
                "height": "34px",
                "width": "28px",
                "text": "Marks",
                "link": "#"
            }
        ];

        let userProfileInfo = [
            {
                "heading": "About",
                "data": [
                    {
                        "img": ic_name,
                        "text": "Parent of Nadal Shah",
                        "link": "#"
                    },
                    {
                        "img": ic_hat,
                        "text": "Class IV C",
                        "link": "#"
                    },
                    {
                        "img": ic_phone,
                        "text": "8574867264",
                        "link": "#"
                    },
                    {
                        "img": ic_mail,
                        "text": "porche911@aladeen.com",
                        "link": "#"
                    }
                ],
            },
            {
                "heading": "Diaries",
                "data": [
                    {
                        "img": ic_diary,
                        "text": "IV A English",
                        "link": "#"
                    },
                    {
                        "img": ic_diary,
                        "text": "IV A Hindi",
                        "link": "#"
                    },
                    {
                        "img": ic_diary,
                        "text": "IV A Urdu",
                        "link": "#"
                    }
                ],
            },
            {
                "heading": "Account",
                "data": [
                    {
                        "img": ic_username,
                        "text": "aladeen",
                        "link": "#"
                    },
                    {
                        "img": ic_password,
                        "text": "************",
                        "link": "#"
                    }
                ]
            }
        ];

    return (
        <div className="App">
            <NavigationHeader 
                background="none" 
                iconList={iconList}
            />
            
            <ProfileHeader images={profileHeaderImages} />

            <div className="_fl2" style={{"margin-top": "12vw"}}>
                <p style={{"font-size": "25px", "font-weight": "bold"}}>Admiral General Aladeen</p>
            </div>
            
            <br />
            <br />

            <InlineIconWithTextMenu iconWithTextList={iconWithTextList} direction="column" />

            <div class="container">
                <UserProfileInfo data={userProfileInfo} />
            </div>     

            <div style={{'height': '0px'}}></div>
        </div>
    );
  }
}

export default App;



