import React, { Component } from 'react';
import Icon from './Icon'

/*props::
    justifyContent:,
    icons: an array of objects, each object decribing an icon
*/
class IconList extends Component 
{
    render() 
    {
        let icons = this.props.icons,
        styles = 
        {
            justifyContent: "" + (this.props.justifyContent || "space-between"),
        }
   
        let iconList =  icons.map((icon, index) => {
                            return <Icon key={ index } name={ icon.name } color={ icon.color } />
                        });

        return (
            <div className="_fl0" style={styles}>{ iconList }</div>
        )
    }
}

export default IconList;


