import React, { Component } from 'react';
import CircleWithImage from './CircleWithImage';
import Jumbotron from './Jumbotron';

class ProfileHeader extends Component 
{
    render()
    {
        let styles = 
        {
            zIndex: "10",
            position: "absolute",
            left: "50%",
            top: "16vw",
            transform: "translate(-50%, 16vw)"
        }
        return (
            <div>
                <Jumbotron backgroundImage={this.props.images.coverPage} />
                <div style={styles}>
                    <CircleWithImage 
                        radius="30vw" 
                        border="8px solid #fff" 
                        src={this.props.images.profilePic}
                    />
                </div>
            </div>
        )
    }
}

export default ProfileHeader;


