import React, { Component } from 'react';
import CircleWithImage from './CircleWithImage';
import Icon from './Icon';

const InfoList = (props) => {

    const data = props.data;

    let styles = {
        justifyContent: "flex-start",
        borderBottom: "unset"
    }

    let headerStyle = 
    { 
        marginLeft: "10px", 
        fontSize: "18px", 
        fontWeight: "bold", 
        color: "grey"
    }

    let listItems = data.data.map((item, index) => 
        <li className="collection-item _fl0 _fl3 _pd0" style={styles}>
            <CircleWithImage key={index} radius="25px" src={item.img} />
            <p style={{"font-size": "18px", "font-weight": "bold"}}>{item.text}</p>
            </li>
        );

    const listHtml = (
        <div style={{"margin-top": "10px"}}>
            <div className="_fl0 _bd3" style={{"justify-content": "space-between"}}>
                <div style={headerStyle}>{data.heading}</div>
                <div><Icon name="create" color="grey" padding="5px" /></div>
            </div>
            <div>
                <ul className="collection">{listItems}</ul>
            </div>
        </div>
    );

    return listHtml;
}

export default InfoList;


