import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import IconWithText from './IconWithText';

class InlineIconWithTextMenu extends Component 
{
    render()
    {
        let styles =
        {
            background: "" + (this.props.background || ""),
            padding: "" + (this.props.padding || ""),
            border: "none",
            borderTop: "1px solid #eee",
            borderBottom: "1px solid #eee",
            justifyContent: "space-around"
        }

        let direction = this.props.direction;
        let iconList = this.props.iconWithTextList

        iconList = iconList.map((icon, index) => {
            return <li>
                        <a href={icon.link}  className="collection-item">
                            <IconWithText key={ index } 
                                src={ icon.src } 
                                height={ icon.height }
                                width={ icon.width }
                                padding="none"
                                text={ icon.text } 
                                direction={ direction } />
                        </a>
                    </li>
        });

        return (
            <div >
                <ul className="collection _fl0 _fl4" style={styles}>{iconList}</ul>
            </div>
        )
    }
}

export default InlineIconWithTextMenu;


