import React, { Component } from 'react';

/* props:: 
    padding: padding to the icon, 
    color: color of the icon, 
    name: material icon name
*/
function Icon(props)
{
    let styles = 
    {
        padding: "" + (props.padding || "10px") + "",
        color: "" + (props.color || "") + "",
    }

    return (
        <a href="#">
            <div style={styles}>
                <i className={`material-icons ${props.size || ""}`}>{props.name}</i>
            </div>
        </a>
    )
}

export default Icon;


