import React, { Component } from 'react';

// props: iconClass(for material icons), text, textColor, direction, src and height for images
class IconWithText extends Component
{
   render()
    {
        let dir = this.props.direction;

        let styles = 
        {
            padding: "" + (this.props.padding || "10px"),
            display: "flex",
            flexDirection: "" + (dir || "row") + "",
            alignItems: "center",
            flexWrap: "wrap"
        }


        let imgStyle = 
        {
            width: "" + (this.props.width || "30px"),
            height: "" + (this.props.height || "30px"),
            backgroundImage: "url(" + this.props.src + ")",
            backgroundPosition: "center",
            backgroundSize: "cover"
        }

        let textStyle = 
        {
            color: "" + (this.props.textColor || "black")
        }

        let iconWithText = {};

        if(this.props.iconClass)
        {
            iconWithText = <div style={{"display": "flex"}}>
                                <div style={styles}>
                                    <i className="material-icons" style={{'padding-right': '6px'}}>{this.props.iconClass}</i>
                                    <span style={textStyle}>{this.props.text}</span>
                                </div>
                            </div>;
        }
        else
        {
            iconWithText = <div style={{"display": "flex"}}>
                                <div style={styles}>
                                    <div style={imgStyle}></div>
                                    <div><span style={textStyle}>{this.props.text}</span></div>
                                </div>
                            </div>;
        }
     
        return (
            iconWithText        
        )
    }
}

export default IconWithText;


