import React, { Component } from 'react';
import IconList from './IconList';

/* props: 
    background: background of the header,
    fixed: position css property(0 means absolute and 1 means fixed)
*/
class NavigationHeader extends Component 
{
    render()
    {
        let pos = (this.props.fixed === "1") ? "fixed" : "absolute";
        let styles = 
        {
            width: "100%",
            height: "50px",
            position: pos,
            top: "0px",
            background: "" + (this.props.background || "#2ecc71") + ""
        };

        return (
            <div style={styles}>
                <IconList icons={this.props.iconList}/>                
            </div>
        )
    }
}

export default NavigationHeader;


