import React, { Component } from 'react';

// props: radius, text, background, color, fontSize, fontFamily, fontWeight, border, margin (All are optional)
class CircleWithText extends Component
{
    render()
    {
        let styles = 
        {
            width: '' + (this.props.radius || '30px'),
            height: '' + (this.props.radius || '30px'),
            borderRadius: '50%',
            border: '' + (this.props.border || '') + '',
            background: '' + (this.props.background || '#2ecc71') + '',
            color: '' + (this.props.color || '#fff') + '',
            fontSize: '' + (this.props.fontSize || 'initial') + '',
            fontFamily: '' + (this.props.fontFamily || 'initial') + '',
            fontWeight: '' + (this.props.fontWeight || 'normal') + '',
            padding: '10px',
            margin: '' + (this.props.margin || '10px') + '',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
        };

        return (
            <div style={styles}>{this.props.text}</div>
        )
    }
}

export default CircleWithText;


